export default {
  name: 'homebtn',
  props:{
    url:{
      type:String
    }
  },
  data(){
    return {

    }
  },
  computed:{

  },
  methods:{
    backHomeHandle(){
      event.preventDefault();
      if(this.url=="/"){
        window.history.replaceState("","","");
        window.localStorage.removeItem('selectedObj');
        this.$router.push({'path':'/'})
      }else if(this.url=="module/select.html"){
        window.location.href="/module/select.html";
      }

    }
  },
  components:{

  },
  mounted(){

  }
}



